import { initializeApp } from 'firebase/app';
import {
  getAuth,
  GoogleAuthProvider as FirebaseGoogleProvider,
  signInWithRedirect,
  getRedirectResult,
  signInWithCredential
} from 'firebase/auth';
import { Capacitor } from '@capacitor/core';

// Firebase config
const firebaseConfig = {
  apiKey: "AIzaSyA2_ukaeoH-Pka7o9JFxDIocb8pm1c5o08",
  authDomain: "wellness-buddy-5de14.firebaseapp.com",
  projectId: "wellness-buddy-5de14",
  storageBucket: "wellness-buddy-5de14.firebasestorage.app",
  messagingSenderId: "610941252952",
  appId: "1:610941252952:web:5a009bc00697f5be57e12d",
  measurementId: "G-7QTHHT5PJC"
};

const app = initializeApp(firebaseConfig);
export const auth = getAuth(app);

// Google Auth Provider for web
export const googleProvider = new FirebaseGoogleProvider();
googleProvider.addScope('email');
googleProvider.addScope('profile');
googleProvider.setCustomParameters({ prompt: 'select_account' });

//  Platform utilities
const isMobile = () =>
  /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

const isNativeAndroid = () =>
  Capacitor.getPlatform() === 'android' && !!window.plugins?.googleplus;

//  Session redirect tracking
const setRedirectPending = () => sessionStorage.setItem('redirectPending', 'true');
const isRedirectPending = () => sessionStorage.getItem('redirectPending') === 'true';
const clearRedirectPending = () => sessionStorage.removeItem('redirectPending');

//  Web sign-in using redirect
const signInWithWebRedirect = async () => {
  setRedirectPending();
  await signInWithRedirect(auth, googleProvider);
  return null; // Result is handled by handleRedirectResult
};

//  Native sign-in using cordova-plugin-googleplus
const signInWithNativeGoogle = async () => {
  try {
    const result = await window.plugins.googleplus.login({
      webClientId: '155478550721-suf2sas620qebja2hbplfh20gktmom1k.apps.googleusercontent.com', //  From Firebase Console
      offline: true,
      scopes: 'profile email'
    });

    const credential = FirebaseGoogleProvider.credential(result.idToken);
    const userCredential = await signInWithCredential(auth, credential);
    return userCredential.user;
  } catch (error) {
    console.error('Native Google sign-in failed:', error);
    throw error;
  }
};

//  Main unified sign-in function
export const signInWithGoogle = async () => {
  try {
    if (isNativeAndroid()) {
      return await signInWithNativeGoogle();
    } else {
      return await signInWithWebRedirect();
    }
  } catch (error) {
    clearRedirectPending();
    throw error;
  }
};

//  Handles redirect sign-in result for web
export const handleRedirectResult = async () => {
  try {
    if (!isRedirectPending()) {
      console.log('No redirect pending, skipping.');
      return null;
    }

    const result = await getRedirectResult(auth);

    if (result) {
      clearRedirectPending();
      return result.user;
    } else {
      return null;
    }
  } catch (error) {
    clearRedirectPending();

    if (
      error.code === 'auth/no-current-user' ||
      error.code === 'auth/popup-closed-by-user' ||
      error.code === 'auth/cancelled-popup-request'
    ) {
      return null;
    }

    console.error('handleRedirectResult error:', error);
    throw error;
  }
};

//  Sign out user
export const signOutUser = async () => {
  try {
    clearRedirectPending();
    await auth.signOut();
  } catch (error) {
    throw error;
  }
};
